.purpleButton {

  margin-right: 8px;
  margin-bottom: 45px;
  position: relative;
  padding: 25px 0px;
  width: 380px;
  background: #7872B9;
  border-radius: 15px;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #FFFFFF;
}

.purpleButton::before {
  cursor: pointer;
  z-index: -1;
  content: '';
  position: absolute;
  left: 8px;
  top: 15px;
  background: #CDE1FF;
  border-radius: 15px;
  width: 380px;
  height: 70px;
}
